.ma-header {
  padding: 1px 50px 280px;
  background: #e2e2e2 url("../img/header-bg.jpg") 50% no-repeat;
  background-size: cover;
  text-align: center;
  border-bottom: 2px solid #03a1e4;
}

.ma-header__title {
  font-family: "gilroyextrabold", sans-serif;
  font-size: 60px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 173px;
  margin-bottom: 0;
  line-height: 69px;
}

.ma-header__description {
  font-family: "gilroylight", sans-serif;
  font-size: 30px;
  font-weight: 400;
  margin-bottom: 25px;
  line-height: 36px;
}

.ma-header__btn {
  display: block;
  padding: 10px 20px;
  color: #ffffff;
  box-shadow: 0 0 25px rgba(3, 161, 228, 0.2);
  border-radius: 20px;
  background-color: #03a1e4;
}

.ma-header__btn:hover {
  color: #ffffff;
  text-decoration: none;
  box-shadow: 0px 0px 5px 3px rgba(3, 161, 228, 0.5);
}

.navbar {
  border: none;
  background: none;
}

.dropdown-menu {
  min-width: 50px;
  padding: 0;
  font-size: 18px;
  background-color: transparent;
  box-shadow: none;
}

.navbar-nav > li > .dropdown-menu {
  border: none;
}

.navbar-default .navbar-nav > li > a {
  color: #444444;
  font-family: "RobotoMedium", sans-serif;
  font-size: 18px;
}

.navbar-nav {
  padding-top: 21px;
}

.navbar-nav__item {
  margin-left: 30px;
  padding-top: 20px;
}

.navbar-nav__link {
  border-bottom: 2px solid transparent;
}

.navbar-nav__link:hover {
  border-bottom: 2px solid #03a1e4 !important;
  transition: all 0.2s ease-in;
}

.navbar-default .navbar-nav > .open > a,
.navbar-default .navbar-nav > .open > a:hover,
.navbar-default .navbar-nav > .open > a:focus {
  color: #444444;
  background-color: transparent;
}

.nav .open > a, .nav .open > a:focus, .nav .open > a:hover {
  color: #444444;
  background-color: transparent;
}

.dropdown-menu > li > a {
  border-bottom: 2px solid transparent;
  padding: 3px 0px;
  text-align: center;
}

.dropdown-menu > li > a:hover {
  border-bottom: 2px solid #03a1e4;
  background-color: transparent;
}

.ma-tabs {
  padding: 100px 0;
  background: #ececec url("../img/design.jpg") 50% no-repeat;
  background-size: cover;
  border-bottom: 2px solid #03a1e4;
}

.ma-tabs__title {
  font-family: "gilroyextrabold", sans-serif;
  font-size: 48px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 32px;
  text-align: center;
}

.ma-tabs__title:before {
  content: url("../img/two.png");
  margin-right: 17px;
}

.ma-tabs__description {
  font-family: "RobotoRegular", sans-serif;
  font-weight: 400;
  margin-bottom: 50px;
  text-align: center;
}

.ma-tabs__choice {
  font-family: "gilroyextrabold", sans-serif;
  font-weight: 700;
  text-align: center;
}

.ma-tabs__preview {
  height: 250px;
  width: 165px;
  transform: rotate(90deg);
  overflow: hidden;
}

.ma-tabs__link {
  font-family: "gilroyextrabold", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  border: none;
}

.ma-tabs__link--last {
  margin-left: 70px;
}

.tabs-left > li {
  float: none;
}

.tabs-left > li.active > a,
.tabs-left > li.active > a:hover,
.tabs-left > li.active > a:focus {
  color: #00a0e3;
  background-color: transparent;
}

.tabs-left > li.active > a {
  color: #00a0e3;
}

.tabs-left > li > a {
  margin-right: 0;
  display: block;
  color: #000000;
}

.sideways {
  position: relative;
}

.sideways > li {
  text-align: left;
  margin-bottom: 30px;
}

.sideways.tabs-left > li {
  transform: rotate(-90deg);
}

.nav-tabs > li.active > a,
.nav-tabs > li.active > a:focus,
.nav-tabs > li.active > a:hover {
  border: none;
}

.nav > li > a {
  padding: 0;
}

.nav-tabs {
  border: none;
}

.nav-tabs > li > a {
  border: none;
}

.nav > li > a:hover,
.nav > li > a:focus {
  text-decoration: none;
  background-color: transparent;
  border: transparent;
}

.ma-reviews {
  background: #fdfdfd url("../img/video-bg.jpg") 50% no-repeat;
  background-size: cover;
  padding: 110px 0;
}

.ma-reviews__title {
  color: #03a1e4;
  font-family: "gilroyextrabold", sans-serif;
  font-size: 48px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 30px;
}

.ma-slider {
  height: 308px;
}

.ma-slide {
  width: 262px;
  height: 145px;
  margin: 30px;
  margin-top: 100px;
}

.slick-center.ma-slide {
  width: 555px;
  height: 308px;
  margin-top: 30px;
}

.slick-prev:before {
  content: url("../../assets/img/arrow-left-black.png");
}

.slick-prev:hover:before {
  content: url("../../assets/img/arrow-left-blue.png");
}

.slick-next:before {
  content: url("../../assets/img/arrow-right-black.png");
}

.slick-next:hover:before {
  content: url("../../assets/img/arrow-right-blue.png");
}

@font-face {
  font-family: "RobotoRegular";
  src: url("../fonts/Roboto/RobotoRegular/RobotoRegular.eot");
  src: url("../fonts/Roboto/RobotoRegular/RobotoRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto/RobotoRegular/RobotoRegular.woff") format("woff"), url("../fonts/Roboto/RobotoRegular/RobotoRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "RobotoMedium";
  src: url("../fonts/Roboto/RobotoMedium/RobotoMedium.eot");
  src: url("../fonts/Roboto/RobotoMedium/RobotoMedium.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto/RobotoMedium/RobotoMedium.woff") format("woff"), url("../fonts/Roboto/RobotoMedium/RobotoMedium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

@font-face {
  font-family: "gilroylight";
  src: url("../fonts/Gilroy/light/gilroy-light.eot");
  src: url("../fonts/Gilroy/light/gilroy-light.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy/light/gilroy-light.woff") format("woff"), url("../fonts/Gilroy/light/gilroy-light.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "gilroyextrabold";
  src: url("../fonts/Gilroy/extrabold/gilroy-extrabold.eot");
  src: url("../fonts/Gilroy/extrabold/gilroy-extrabold.eot?#iefix") format("embedded-opentype"), url("../fonts/Gilroy/extrabold/gilroy-extrabold.woff") format("woff"), url("../fonts/Gilroy/extrabold/gilroy-extrabold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

*,
*:after,
*:before {
  box-sizing: border-box;
  transition: all 0.3s ease-in;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body, html {
  min-height: 100%;
}

body {
  font-size: 18px;
  color: #000000;
}

@media (max-width: 1066px) {
  .navbar-nav__item {
    margin-left: 15px;
  }
}

@media (max-width: 976px) {
  .navbar-nav__item {
    margin-left: 5px;
  }
}

@media (max-width: 916px) {
  .navbar-header {
    float: none;
  }
  .navbar-left, .navbar-right {
    float: none !important;
  }
  .navbar-toggle {
    display: block;
    margin-top: 30px;
    margin-bottom: 0;
  }
  .navbar-fixed-top {
    top: 0;
    border-width: 0 0 1px;
  }
  .navbar-collapse.collapse {
    display: none !important;
  }
  .navbar-nav {
    float: none !important;
    width: 31%;
    margin: 0 auto;
  }
  .navbar-nav > li {
    float: none;
  }
  .navbar-nav > li > a {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .collapse.in {
    display: block !important;
  }
}

@media (max-width: 767px) {
  .navbar-collapse {
    border-top: none;
    box-shadow: none;
  }
  .ma-header {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 100px;
  }
  .sideways.tabs-left > li {
    transform: none;
    text-align: center;
    margin: 0 auto 10px;
  }
  .ma-tabs__preview {
    height: 135px;
    width: 100px;
    transform: none;
    margin: 0 auto;
  }
  .ma-tabs__link--last {
    margin-left: 0;
  }
}

@media (max-width: 467px) {
  .navbar-toggle {
    float: none;
    margin: 20px auto 0;
  }
}

@media (max-width: 550px) {
  .ma-header__title {
    margin-top: 100px;
    font-size: 40px;
    line-height: 40px;
  }
  .ma-header__description {
    font-size: 20px;
    line-height: 26px;
  }
}

@media (max-width: 991px) {
  .ma-header__btn {
    padding-left: 8px;
    padding-right: 8px;
  }
  .ma-slide {
    width: 555px;
    height: 308px;
    margin-top: 30px;
  }
}
